.group-options-window {
    z-index: 98;
    position: fixed;
    background-color: #00000088;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
}
.group-options-container {
    z-index: 99;
    position: absolute;
    display: flex;
    top: 40px;
    left: 100px;
    width: 270px;
    flex-direction: column;
    background-color: #fff;
    border-radius: 3px;
    color: #172b4d;
    padding: 10px;
    box-shadow: 0 8px 16px -4px rgba(9, 30, 66, 0.25), 0 0 0 1px rgba(9, 30, 66, 0.08);
    text-decoration: none;
    > *:not(:first-child) {
        padding: 10px 0px 10px 0px;
        margin: 0px;
    }
    .group-options-header {
        width: 100%;
        display: flex;
        border-bottom: 0.2px solid rgba(94, 108, 132, 0.25);
        justify-content: space-around;
        align-items: baseline;
        padding-bottom: 10px;
        .title {
            line-height: 0.9;
            flex-grow: 1;
            color: #243757;
            font-size: 1.2rem;
            text-align: center;
            flex-grow: 1;
        }
        .close-group-options-btn {
            margin: 0;
            padding: 0;
            color: #233655;
            background-color: transparent;
            border: none;
            font-size: 1.1rem;
            cursor: pointer;
            outline: none;
        }
    }
    button {
        text-align: left;
        background-color: rgba(255, 255, 255, 0);
        border-radius: 5px;
    }
    button:hover {
        background-color: rgba(0, 0, 0, 0.068);
    }

}
