                        margin: 0 0 5px 0;
                    }
                    .edit-members-container {
                        left: 408px;
                        top: 186px;
                    }
                    .edit-labels-container {
                        left: 408px;
                        top: 217px;
                    }
                    .dates-form {
                        left: 486px;
                        top: 304px;
                    }
                    .attachment {
                        left: 475px;
                        top: 345px;
                        z-index: 4;
                        position: absolute;
                        background-color: #fff;
                        border-radius: 3px;
