                        border: none;
                        background-color: #eaecf0;
                        font-size: 14px;
                        font-family: inherit;
                        resize: none;
                        border-radius: 5px;
                        word-spacing: 2px;
                        padding: 8px 12px;
                        &:focus {
                            background-color: white;
                            border: 1px solid #172b4d;
                        }
                    }
                }
                
                .members {
                    margin-bottom: 10px;
                    margin-left: 35px;
                    border: none;
